.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    max-width: 100%;
    width: var(--default-layout-width);
    min-height: 1000px;
    display: flex;
    margin-top: var(--default-layout-header-height);
}
.content {
    flex: 1;
    padding: 0 0 24px 24px;
    margin-left: var(--default-layout-sidebar-width);
}
